.alert {
  width: 250px;
  height: 50px;
  padding: 0 15px 5px;
  background-color: red;
  color: #fff;
  text-align: center;
  font-size: 16px;
  position: fixed;
  right: 100px;
  top: 50px;
  border-radius: 15px;
}

.enter {
    transform: translateX(100%);
  }
  
  .enterActive {
    transform: translateX(0);
    transition: 250ms ease-in-out;
  }
  
  .exit {
    transform: translateX(0);
  }
  
  .exitActive {
    transform: translateX(100%);
    transition: 250ms ease-in-out;
  } 